.button {
  display: inline-block;
  padding: 8px 16px;
  font-size: 14px;
  color: #333;
  background-color: #fff;
  border: none;
  border-radius: 4px;
  cursor: pointer;
  transition: all 0.2s ease;
}

.button:hover {
  background-color: transparent;
}

.button.btn-primary {
  color: #fff;
  background: linear-gradient(135deg, #2196F3 0%, #1976D2 100%);
  border: none;
}

.button.btn-primary:hover {
  background: linear-gradient(135deg, #1976D2 0%, #1565C0 100%);
}


.hero {
  position: relative;
  z-index: 10;
  height: calc(100vh - 80px);
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 0 2rem;
}


.hero-content {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  max-width: 1200px;
  width: 100%;
}

.hero-title {
  font-family: 'Poppins', sans-serif;
  font-size: clamp(2rem, 6vw, 4rem);
  font-weight: 800;
  color: #ffffff;
  margin-bottom: 1.5rem;
  line-height: 1.1;
  text-shadow: 0 4px 20px rgba(255, 107, 53, 0.3);
  width: 100%;
  text-align: center;
}

.hero-subtitle {
  font-family: 'Poppins', sans-serif;
  font-size: clamp(0.9rem, 2.5vw, 1.5rem);
  font-weight: 400;
  color: #b0b0b0;
  margin-bottom: 3rem;
  line-height: 1.4;
  width: 100%;
  text-align: center;
}

.hero-buttons {
  display: flex;
  gap: 2rem;
  justify-content: center;
  align-items: center;
  flex-wrap: wrap;
  width: 100%;
}

.btn-wrapper {
  font-family: 'Poppins', sans-serif;
  background: transparent;
  color: #ffffff;
  border: 2px solid rgba(255, 255, 255, 0.3);
  padding: 1rem 2rem;
  border-radius: 0.75rem;
  font-weight: 500;
  font-size: 1rem;
  cursor: pointer;
  transition: all 0.3s ease;
  text-decoration: none;
  display: inline-block;
  backdrop-filter: blur(10px);
  text-align: center;
  min-width: 220px;
}

.btn-wrapper:hover {
  border-color: rgba(255, 255, 255, 0.5);
  background: rgba(255, 255, 255, 0.1);
}

.btn-wrapper.btn-blue {
  background: #2196F3;
  border: none;
  box-shadow: 0 4px 15px rgba(33, 150, 243, 0.3);
}

.btn-wrapper.btn-blue:hover {
  background: #1976D2;
  box-shadow: 0 6px 20px rgba(33, 150, 243, 0.4);
}

@media (max-width: 1024px) {
  .hero-title {
    font-size: 3rem;
  }
  .hero-subtitle {
    font-size: 1.3rem;
  }
}

@media (max-width: 768px) {
  .hero-title {
    font-size: 2.5rem;
  }
  .hero-subtitle {
    font-size: 1.2rem;
  }
  
  .hero-buttons {
    flex-direction: column;
    align-items: center;
    gap: 1.5rem;
  }
  
  .btn-wrapper {
    width: 100%;
    max-width: 300px;
  }
}

@media (max-width: 480px) {
  .hero-title {
    font-size: 2rem;
  }
  .hero-subtitle {
    font-size: 1rem;
  }
  .hero {
    padding: 0 1rem;
  }
} 